:root {
  --accent: #0c0f1c;
  /* Navy */
  --text: #c5a762;
  /* Gold */
  --bg: #0303030e;

  --review-h: 320px;
  --review-h-sm: 420px;
}

.page {
  background: var(--bg);
  color: var(--text);
  font-family: 'Lora', serif;
  overflow-x: hidden;
}

.hero {
  position: relative;
  height: 450px;
  overflow: hidden;
}

.heroBg {
  position: relative;
  top: auto;
  height: 100%;
  width: 100%;
  z-index: 0;
}

.heroVideo {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.heroOverlay {
  position: absolute;
  inset: 0;
  background: var(--bg);
}

.heroContent {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1;
  color: var(--text);
  padding: 20px;
}

.heroFade {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 120px;
  pointer-events: none;
  z-index: 0;
}

.title {
  font-size: 52px;
  font-weight: 800;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.subtitle {
  font-size: 20px;
  letter-spacing: 0.05em;
}

/* ===== ÜBERLAPPENDER KARTEN-ABSCHNITT ===== */
.overlapSection {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 2;
  margin-top: -90px;
  margin-bottom: 80px;
  background: var(--bg);
}

.card {
  background: #fff;
  border-radius: 18px;
  border: 2px solid var(--accent);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.18);
  padding: 48px 40px;
  text-align: center;
}

.card h2 {
  color: var(--text);
  font-size: 28px;
  font-weight: 800;
  margin-bottom: 20px;
}

.card p {
  color: var(--accent);
  font-size: 17px;
  line-height: 1.6;
  margin-bottom: 20px;
}

.card .button {
  background-color: var(--text);
  color: var(--accent);
  border: none;
  padding: 12px 28px;
  font-weight: 700;
  border-radius: 10px;
  cursor: pointer;
  transition: transform .15s ease, box-shadow .2s ease;
  box-shadow: 0 8px 22px rgba(0, 0, 0, .12);
}

.card .button:hover {
  transform: translateY(-1px);
}

/* ===== LASER  ===== */
.LaserSection{
  position: relative;
  isolation: isolate;
  min-height: 90svh;
  padding: clamp(24px, 4vw, 56px);
  overflow: hidden;
  background: var(--bg);

  --laserW: min(44vw, 560px);
  --laserOffset: clamp(16px, 3vw, 40px);
}

.LaserSection::after {
  content:none;
  position:absolute; inset:0;
  z-index:-1;
  background: linear-gradient(90deg,
    rgba(10,12,16,.14) 0%,
    rgba(10,12,16,.08) 40%,
    rgba(10,12,16,0) 75%);
}

.laserMedia{
  position: absolute;
  top: 50%;
  right: var(--laserOffset);
  transform: translateY(-50%);
  width: var(--laserW);
  z-index: 0;             
  pointer-events: none;
}
.laserMedia img{
  width: 100%;
  height: auto;
  object-fit: contain;     
  object-position: right center;
  opacity: 1;              
  mix-blend-mode: normal;
  filter: drop-shadow(0 10px 24px rgba(0,0,0,.12)) contrast(1.02) saturate(.98);
}

.laserGrid{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  align-content: center;
  min-height: inherit;

  padding-right: calc(var(--laserW) + var(--laserOffset) + clamp(8px,1.5vw,16px));
}

.laserText {
  position: relative;
  z-index: 1;
  max-width: 720px;             
  text-wrap: balance;
}

.laserText::before {
  content: "";
  position: absolute;
  inset: -8px -10px -8px -10px;
  background: radial-gradient(closest-side, rgba(255,255,255,.06), transparent 70%);
  z-index: -1;
  border-radius: 16px;
}

.laserText h3 {
  color: var(--text);
  font-size: clamp(32px, 4.2vw, 56px);
  font-weight: 800;
  line-height: 1.12;
  margin: 8px 0 14px;
}

.eyebrow {
  display: inline-block;
  font-size: 12px;
  letter-spacing: .12em;
  text-transform: uppercase;
  color: color-mix(in srgb, var(--accent) 65%, #000 0%);
  border: 1px solid color-mix(in srgb, var(--accent) 35%, transparent);
  padding: 6px 10px;
  border-radius: 999px;
}

.laserText p {
  line-height: 1.7;
  margin: 10px 0 16px;
  color: var(--accent);
}

.factsScroller{
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: clamp(220px, 70vw, 300px);
  gap: 12px;
  overflow-x: auto;
  padding: 6px 2px 2px;
  margin: 8px 0 18px;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
}
.factCard{
  scroll-snap-align: start;
  border: 1px solid color-mix(in srgb, var(--accent) 25%, transparent);
  border-radius: 16px;
  padding: 14px;
  background: color-mix(in srgb, var(--accent) 6%, #fff 0%);
  box-shadow: 0 8px 20px rgba(0,0,0,.06);
}
.factCard h4{ font-size: 16px; margin-bottom: 6px; }
@keyframes hintFade{ 0%,100%{opacity:.2} 50%{opacity:.7} }

.cta {
  background: var(--text);
  color: var(--accent);
  border: 2px solid var(--text);
  font-weight: 700;
  padding: 12px 22px;
  border-radius: 12px;
  cursor: pointer;
  transition: transform .15s ease, box-shadow .2s ease;
  box-shadow: 0 8px 22px rgba(0,0,0,.12);
}
.cta:hover { transform: translateY(-1px); }

/* ==== MOBILE-RESET FÜR LASER-BEREICH ==== */
@media (max-width: 900px) {

  .LaserSection {
    min-height: auto;
    padding: 28px 16px 40px;
    --laserW: auto;
    --laserOffset: 0;
  }

  .LaserSection::after {
    background: linear-gradient(
      180deg,
      rgba(10,12,16,.10) 0%,
      rgba(10,12,16,.04) 50%,
      rgba(10,12,16,0) 100%
    );
  }

  .laserGrid {
    max-width: 480px;
    margin: 0 auto;
    display: flex !important;
    flex-direction: column;
    align-items: flex-start;
    min-height: auto;
    padding-right: 0 !important;      /* kein Platz mehr für „unsichtbares“ Bild rechts */
  }

  /* Block optisch mittig, Headline zentriert, Text links */
  .laserText {
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }

  .laserText h3 {
    font-size: clamp(30px, 8vw, 42px);
    line-height: 1.05;
    margin-bottom: 14px;
  }

  .laserText p {
    text-align: left;
  }

  .laserMedia {
    position: static !important;
    top: auto !important;
    right: auto !important;
    transform: none !important;
    width: min(320px, 70vw) !important;
    margin: 32px auto 0 !important;
    text-align: center;
    pointer-events: none;
    z-index: 0 !important;
  }

  .laserMedia img {
    width: 100% !important;
    height: auto;
    object-fit: contain;
  }

  .laserCtaBtn {
    display: inline-block;
    margin: clamp(20px, 5vw, 32px) auto 0;
  }
}


/* ===== UNSERE STUDIOS ===== */

.StudioSection {
  padding: clamp(32px, 6vw, 80px) 0;
  background: var(--bg);
}

.studioContainer {
  width: min(1200px, 92vw);
  margin: 0 auto;
  display: grid;
  grid-auto-flow: row;
  justify-items: center;
  row-gap: clamp(20px, 3vw, 40px);
}

.studioText {
  grid-column: 1 / -1;
  max-width: 70ch;
  text-align: center;
  margin: 0 auto clamp(17px, 3vw, 36px);
}

.studioText h3 {
  color: var(--text);
  font-size: clamp(28px, 4vw, 40px);
  font-weight: 800;
  margin-bottom: 12px;
}

.studioText p {
  color: color-mix(in srgb, var(--accent) 80%, #000 0%);
  font-size: clamp(16px, 1.6vw, 18px);
}

.studioGrid {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(2, minmax(280px, 1fr));
  gap: clamp(16px, 3vw, 28px);
  width: 100%;
  max-width: 1100px;
  margin: clamp(22px, 3vw, 40px) auto 0;
  justify-items: center;
}


.studioItem{
  position: relative;
  width: 100%;
  max-width: 520px;
  border-radius: 22px;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 10px 28px rgba(0,0,0,.08);
  isolation: isolate;                     
}

.studioImg{
  width: 100%;
  aspect-ratio: 16 / 11;                  
  object-fit: cover;
  display: block;
  transition: transform .45s ease;
  background: #f3f3f3;
}

.studioItem:hover .studioImg {
  transform: scale(1.02);
}

.studioItem::after{
  content:"";
  position:absolute; inset:0;
  background: linear-gradient(to top, rgba(0,0,0,.25) 24%, transparent 60%);
  z-index: 0;
  pointer-events: none;
}

.studioActions{
  position: absolute;
  left: 50%;
  bottom: 14px;
  transform: translateX(-50%);
  z-index: 1;
}

.studioBtn {
  appearance: none;
  border: 1px solid color-mix(in srgb, var(--text) 70%, transparent);
  background: var(--text);
  color: #111;
  font-weight: 600;
  padding: 12px 18px;
  border-radius: 999px;
  cursor: pointer;
  transition: transform .15s ease, box-shadow .2s ease, background .2s ease;
}

.studioBtn:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, .12);
  transform: translateY(-1px);
}

.studioBtn:focus-visible {
  outline: 3px solid color-mix(in srgb, var(--text) 60%, #fff 0%);
  outline-offset: 3px;
}

@media (max-width: 900px) {
  .studioGrid {
    grid-template-columns: 1fr;
  }
}


/* ===== REVIEWS ===== */
.reviewsSection {
  background-color: var(--accent);
  color: var(--text);
  text-align: center;
  padding: 80px 20px 120px;
  position: relative;
  overflow: hidden;
}

.reviewsBadge {
  position: absolute;
  right: clamp(12px, 4vw, 56px);
  bottom: clamp(12px, 3vw, 40px);
  top: auto;
  left: auto;
  width: clamp(140px, 16vw, 220px);
  opacity: .38;
  mix-blend-mode: screen;
  pointer-events: none;
  z-index: 0; 
}

.reviewBox {
  max-width: 900px;
  margin: 0 auto;
  height: var(--review-h);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

@media (max-width: 520px) {
  .reviewsBadge {
    width: 90px;
    opacity: 0.10;
  }
}

@media (max-width: 640px) {
  .reviewBox {
    height: var(--review-h-sm);
  }
}

.reviewInner {
  max-width: 760px;
  padding: 20px 36px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.reviewText {
  font-size: 20px;
  font-style: italic;
  line-height: 1.6;
  margin-bottom: 12px;
  white-space: pre-line;
}

.reviewAuthor {
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 10px;
}

.googleLogo {
  height: 28px;
  width: auto;
  opacity: 0.9;
}

.navButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.9);
  background: rgba(12, 15, 28, 0.85);
  color: #fff;
  font-size: 22px;
  line-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 3;
}

.navButton.prev {
  left: 12px;
}

.navButton.next {
  right: 12px;
}

.dots {
  display: flex;
  gap: 8px;
  justify-content: center;
  margin-top: 20px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
}

.dot.active {
  background-color: var(--text);
}